.TodoSearch {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #C1D3E1;
    margin: 20px;
    padding-left: 10px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: left;
    font-family: 'Montserrat';
    font-weight: bolder;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::placeholder {
    color: #C1D3E1;
    font-family: 'Montserrat';
    font-weight: bolder;
  }
  
  .TodoSearch:focus {
    outline-color: #61DAFA;
  }

  @media screen and (max-width:500px){
    .TodoSearch {
      font-size: 17px;
    }
  }