header {
    margin: 0px;
    padding: 0px;
    width: 100%;
  }
  
  .TodoCounter,
  .title {
      text-align: center;
    }
  
  .TodoCounter{
    font-size: 24px;
    padding: 10px;
    color: #7DBC9F;
  }
  .title {
    background-color: #7DBC9F;
    color: #F8F9F9;
    font-size: 40px;
    margin: 0px;
    padding: 20px;
  }
   
  @media screen and (max-width:500px){
    .title {
      font-size: 30px;
    }
    .TodoCounter {
      font-size: 17px;
    }
  }